.RollDice{
	margin-top: 50px;
}

.RollDice h1{
	text-align: center;
	font-family:"Lucida Console", "Courier New", monospace;
}

.RollDice-container{
	display: flex;
	justify-content: center;
	align-content: center;
}

.RollDice button{
	align-self:center;
	width: 15em;
	padding: 1.5em;
	border: 0px;
	border-radius: 10px;
	color: white;
	background-color: black;
	margin-top: 3em;
}

.RollDice button:hover{
	background-color: maroon;
	cursor: pointer;
}

